.enter-key {
    display: flex;
    font-size: 18px;
    font-weight: bold;
    margin: 2px;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 65px;
    border: 2px white solid;
    border-radius: 7px;
  }

  .enter-key:hover {
    background-color: rgba(255, 255, 255, 0.425);
  }

  .btn-enter {
    border: none !important;
    background-color: transparent !important;
    outline: none;
    cursor: pointer;
    color: white;
  }