.bck-key {
    display: flex;
    font-size: 24px;
    font-weight: bold;
    margin: 2px;
    align-items: center;
    justify-content: center;
    height: 50px;
    width: 50px;
    border: 2px white solid;
    border-radius: 10px;
  }

  .bck-key:hover {
    background-color: rgba(255, 255, 255, 0.425);
  }

  .btn-svg {
    border: none !important;
    background-color: transparent !important;
    outline: none;
    cursor: pointer;
  }