.settings-overlay {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background: linear-gradient(90deg, rgba(242,102,184,1) 0%, rgb(96, 144, 216) 100%);
    z-index: 3000;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.settings-modal {
    width: 60vw;    
}

.setting {
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
}

h1 {
    text-align: center;
}

a {
    font-size: 20px;
}

a:hover {
    color: greenyellow;
}

.close-icon {
    position: absolute;
    top: 16px;
    right: 16px;
}

svg {
    cursor: pointer;
}